<template>
    <el-row>
        <!---->
        <el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
            <el-form :inline="true" :model="filters">
                <el-form-item label="CP">
                    <el-input v-model="filters.name" style="width: 100px;"></el-input>
                </el-form-item>
                <el-form-item>
                    <el-tooltip content="" placement="top">
                    <el-button class="el-icon-search" type="primary" v-on:click="getCPUserList"></el-button>
                    </el-tooltip>
                    <el-tooltip content="" placement="top">
                     <el-button class="el-icon-plus" type="primary" v-on:click="handleAdd"></el-button>
                     </el-tooltip>
                </el-form-item>
            </el-form>
        </el-col>

        <!---->
        <el-col :span="24" class="toolbar">
            <el-pagination
                  @current-change="handleCurrentChange"
                  :current-page="1"
                  :page-size="pageSize"
                  layout="prev, pager, next, jumper"
                  :total="total"
                   style="float:right;">
            </el-pagination>
        </el-col>

        <!---->
        <el-table :data="cp_users" highlight-current-row v-loading="listLoading" border style="font-size:12px; ">
            <el-table-column prop="cp_name" min-width="10"  label="CP" align="center" >
            </el-table-column>
            <el-table-column prop="username" min-width="10"  label="" align="center" >
            </el-table-column>
            <el-table-column prop="password" min-width="10"  label="" align="center" >
            </el-table-column>
            <el-table-column prop="phone" min-width="5"  label="" align="center" >
            </el-table-column>
            <el-table-column prop="appid" align="center" label="AppID" min-width="10"  >
            </el-table-column>
            <el-table-column prop="key" align="center" label="Key" min-width="10" >
            </el-table-column>
            <el-table-column prop="status" align="center"label="" width="40" :formatter="formatBoolean1" >
            </el-table-column>
            <el-table-column prop="is_allow_url" align="center" label="H5" width="50" :formatter="formatBoolean2" >
            </el-table-column>

            <el-table-column prop="name" min-width="10"  label="" align="center" >
            </el-table-column>
            <el-table-column prop="passwd" min-width="10"  label="" align="center" >
            </el-table-column>
            <el-table-column prop="data_url" align="center" label="" min-width="20" >
            </el-table-column>
            <el-table-column prop="callback_url" align="center" label="" min-width="20" >
            </el-table-column>


            <el-table-column prop="created_at" min-width="10" align="center"  label="">
            </el-table-column>
            <el-table-column prop="update_time" min-width="10" align="center"  label="">
            </el-table-column>
            <el-table-column label="" width="80" align="center">
                <template scope="scope">
                    <el-button class="el-icon-edit" type="primary" size="small" v-on:click="handleUpdate(scope.row.id)"></el-button>
                </template>
            </el-table-column>
        </el-table>
        <!---->
        <el-col :span="24" class="toolbar">
            <el-pagination
                  @current-change="handleCurrentChange"
                  :current-page="1"
                  :page-size="pageSize"
                  layout="prev, pager, next, jumper"
                  :total="total"
                   style="float:right;">
            </el-pagination>
        </el-col>
        <!--  banner-->
        <el-dialog :visible.sync="dialogFormVisible" :title="title" :before-close="handleCancle">
            <el-form ref="cp_user" label-width="80px" :model="cp_user"  label-position="left" :rules="rules">
            <el-form-item label="CP" prop="cp_id">
              <el-select v-model="cp_user.cp_id" placeholder=""  :disabled="disabled">
                <el-option
                v-for="item in cps"
                :label="item.name"
                :value="item.id"
                :key="item.id">
                </el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="" prop="username">
              <el-input v-model="cp_user.username"></el-input>
            </el-form-item>
            <el-form-item label="" prop="password">
              <el-input v-model="cp_user.password" ></el-input>
            </el-form-item>

            <el-form-item  prop="appid" label="AppID">
              <el-input v-model="cp_user.appid" readonly></el-input>
            </el-form-item>
            <el-form-item prop="key"  label="Key" >
              <el-input v-model="cp_user.key"></el-input>
            </el-form-item>

            <el-form-item label="" prop="status">
            <el-select v-model="cp_user.status" placeholder="">
                <el-option
                  v-for="item in status"
                  :label="item.value"
                  :value="item.key"
                  :key="item.key">
                </el-option>
            </el-select>
            </el-form-item>
            <el-form-item label="H5" prop="is_allow_url">
            <el-select v-model="cp_user.is_allow_url" placeholder="">
                <el-option
                  v-for="item in is_allow_url"
                  :label="item.value"
                  :value="item.key"
                  :key="item.key">
                </el-option>
            </el-select>
            </el-form-item>
            <el-form-item label="" prop="phone">
              <el-input v-model="cp_user.phone"></el-input>
            </el-form-item>

            <el-form-item label="" prop="name">
              <el-input v-model="cp_user.name"></el-input>
            </el-form-item>
            <el-form-item label="" prop="passwd">
              <el-input v-model="cp_user.passwd" ></el-input>
            </el-form-item>
            <el-form-item label="" prop="data_url">
              <el-input v-model="cp_user.data_url"></el-input>
            </el-form-item>
            <el-form-item label="" prop="callback_url">
              <el-input v-model="cp_user.callback_url"></el-input>
            </el-form-item>

            <el-form-item>
              <el-button type="primary" @click="onSubmit('cp_user')"> </el-button>
            </el-form-item>
          </el-form>
        </el-dialog>
    </el-row>
</template>

<script>
    import util from '../../common/js/util';
    import NProgress from 'nprogress';
    var Resource = require('../../util/Resource');

    export default {
        data() {
            return {
                filters: {
                    name: '',
                },
                cp_users: [],
                total: 0,
                pageSize: 30,
                page: 1,
                title:"",
                listLoading: false,
                imageDialogVisible: false,
                dialogImageUrl: "",
                //dialog
                id:"",
                dialogFormVisible: false,
                icon_data: {
                    type: "new"
                },
                disabled: false,
                cp_user: {
                    id:"",
                    cp_id: '',
                    username: "",
                    password: "",
                    phone: "",
                    status: 1,
                    is_allow_url: 0,
                    name: "",
                    passwd: "",
                    data_url: "",
                    callback_url: "",
                    appid: "",
                    key: "",
                },
                status: [
                    {
                        key : 1,
                        value : ""
                    },
                    {
                        key : 0,
                        value : ""
                    },

                ],
                is_allow_url: [
                    {
                        key : 1,
                        value : ""
                    },
                    {
                        key : 0,
                        value : ""
                    },

                ],
                cps: [],
                rules: {
                    username: [
                      {
                          required: true,
                          message: '',
                          trigger: 'blur'
                      }
                    ],
                    password: [
                      {
                          required: true,
                          message: '',
                          trigger: 'blur'
                      }
                    ],
                    cp_id: [
                      {
                          type: 'integer',
                          required: true,
                          message: 'CP',
                          trigger: 'change'
                      }
                    ],

                }
            }
        },
        // components: { EditBanner },
        methods: {
            handleCurrentChange(val) {
                this.page = val;
                this.getCPUserList();
            },
            handleAdd(){
              var data={
                    id:""
                }
              var _this = this;
              Resource.get({
                  resource: "cp.user",
                  data:data,
                  success: function(data) {
                    _this.dialogFormVisible = true;
                    _this.title = "CP";
                    _this.disabled = false;
                    _this.cp_user.key = data.cp_user.key
                    _this.cp_user.appid = data.cp_user.appid
                  },
                  error: function() {
                      _this.$notify({
                          title: '',
                          message: '',
                          type: 'error'
                      });
                  }
                });

            },
            handleCancle(){
                 this.dialogFormVisible = false;
            },
            handleUpdate(id){
                var data={
                    id:id
                }
                var _this = this;
                Resource.get({
                    resource: "cp.user",
                    data:data,
                    success: function(data) {
                        _this.cp_user = data.cp_user;
                        _this.dialogFormVisible = true;
                        _this.disabled = true;
                        _this.title = "CP";
                    },
                    error: function() {
                        _this.$notify({
                            title: '',
                            message: '',
                            type: 'error'
                        });
                    }
                });

            },

            formatBoolean1: function(row, column) {
              return row.status == true ? '' : row.status == false ? '' : '';

            },
            formatBoolean2: function(row, column) {
              return row.is_allow_url == 1 ? '' : row.is_allow_url == 0 ? '' : '';
            },
            getFilters() {
                var page = this.page || 1;
                var name = this.filters.name || '';
                return {
                    page: page,
                    name: name,
                }
            },
            getCPUserList() {
                let para = this.getFilters();
                this.listLoading = true;
                NProgress.start();
                var _this = this;
                Resource.get({
                    resource: "cp.user_list",
                    data: para,
                    success: function(data) {
                        _this.total = data.total_count;
                        _this.cp_users = data.items;
                        _this.cps = data.cps;
                        _this.pageSize = data.page_size;
                        _this.listLoading = false;
                        NProgress.done();
                    },
                    error: function() {
                        _this.listLoading = false;
                        NProgress.done();
                        _this.$notify({
                            title: '',
                            message: '',
                            type: 'error'
                        });
                    }
                })
            },
            resetCpUser() {
                this.cp_user = {
                                id:"",
                                cp_id: '',
                                username: "",
                                password: "",
                                phone: "",
                                status: 1,
                                is_allow_url: 0,
                                name: "",
                                passwd: "",
                                data_url: "",
                                callback_url: "",
                                appid: "",
                                key: "",
                            }
            },
            onSubmit(formName) {
                this.$refs[formName].validate((valid) => {
                  if (valid) {
                    var _this = this;
                    NProgress.start();
                    var method = "put";
                    if (this.cp_user.id && this.cp_user.id != "" ) {
                        method = "post"
                    }
                    console.log("=====",this.cp_user)
                    Resource.options({
                        method: method,
                        resource: "cp.user",
                        data: this.cp_user,
                        success: function(data) {
                          NProgress.done();
                          _this.dialogFormVisible=false;
                          _this.getCPUserList();
                          _this.resetCpUser();
                          _this.$notify({
                            title: '',
                            message: '',
                            type: 'success'
                          });
                        },
                        error: function(data) {
                          _this.listLoading = false;
                          var message = '';
                          if (data.data && data.data.errmsg) {
                              message = data.data.errmsg
                          }
                          NProgress.done();
                          _this.$notify({
                              title: '',
                              message: message,
                              type: 'error'
                          });
                        }
                    });

                  } else {
                    this.$notify({
                          title: '',
                          message: '',
                          type: 'error'
                      });
                    return false;
                  }
                });
            },
        },
        mounted() {
            this.getCPUserList();
        }
    }

</script>

<style lang="scss" scoped>
.el-table td>div {
    height:100px!important;
}
</style>



