<template>
    <el-row>
        <!---->
        <el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
            <el-form :inline="true" :model="filters">
               <el-form-item label="" prop="tag">
                <el-select
                    v-model="filters.tag"
                    filterable
                    remote
                    placeholder=""
                    :remote-method="getTags"
                     style="width: 180px;padding-left: 14px;">
                    <el-option
                      v-for="item in tags"
                      :key="item.id"
                      :label="item.name"
                      :value="item.id">
                    </el-option>
                  </el-select>
                </el-form-item>
                <el-form-item label="" >
                    <el-input v-model="filters.title" style="width: 180px;"></el-input>
                </el-form-item>
                <el-form-item label="" >
                    <el-input v-model="filters.desc" style="width: 180px;"></el-input>
                </el-form-item>
                <el-form-item label="ID">
                    <el-input v-model="filters.id" style="width: 180px;padding-left: 14px;"></el-input>
                </el-form-item>
                <el-form-item label="ID">
                    <el-input v-model="filters.album_id" style="width: 180px;"></el-input>
                </el-form-item>
                <el-form-item label="" style="padding-left: 25px;">
                    <el-select v-model="filters.category" placeholder="" style="width: 180px;">
                        <el-option
                          v-for="item in filter_result.categories"
                          :label="item.name"
                          :value="item.id"
                          :key="item.id">
                        </el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="" style="padding-left: 10px;">
                    <el-select v-model="filters.is_out" placeholder="" style="width: 100px;">
                        <el-option
                          v-for="item in filter_result.is_out"
                          :label="item.value"
                          :value="item.key"
                          :key="item.key">
                        </el-option>
                    </el-select>
                </el-form-item>
                </br>
                <el-form-item label="" prop="creator" >
                     <el-select
                       v-model="filters.creator"
                       filterable
                       remote
                       placeholder=""
                       :remote-method="getCreators"style="width: 180px;">
                        <el-option
                            v-for="item in creators"
                            :key="item.id"
                            :label="item.name"
                            :value="item.id">
                        </el-option>
                     </el-select>
                </el-form-item>
                <el-form-item label="CP" prop="cp" >
                     <el-select
                       v-model="filters.cp"
                       filterable
                       remote
                       placeholder="CP"
                       :remote-method="getCPs"style="width: 180px;padding-left: 10px;">
                        <el-option
                            v-for="item in cps"
                            :key="item.id"
                            :label="item.name"
                            :value="item.id">
                        </el-option>
                     </el-select>
                </el-form-item>
                <el-form-item label="" prop="brand">
                   <el-select
                       v-model="filters.brand"
                       filterable
                       remote
                       placeholder=""
                       :remote-method="getBrands"
                       style="width: 180px;">
                       <el-option
                         v-for="item in brands"
                         :key="item.id"
                         :label="item.name"
                         :value="item.id">
                       </el-option>
                     </el-select>
                 </el-form-item>
               <!--  <el-form-item label=""  style="padding-left: 27px;">
                    <el-select v-model="filters.isready" placeholder="" style="width: 180px;">
                        <el-option
                          v-for="item in filter_result.isready"
                          :label="item.value"
                          :value="item.key"
                          :key="item.key">
                        </el-option>
                    </el-select>
                </el-form-item> -->
                <el-form-item label="" style="padding-left: 40px;">
                    <el-select v-model="filters.status" placeholder="" style="width: 180px;">
                        <el-option
                          v-for="item in filter_result.status"
                          :label="item.value"
                          :value="item.key"
                          :key="item.key">
                        </el-option>
                    </el-select>
                </el-form-item>

                <el-form-item label="" style="padding-left: 0px;">
                    <el-select v-model="filters.acc_type" placeholder="" style="width: 180px;">
                        <el-option
                          v-for="item in filter_result.acc_type"
                          :label="item.value"
                          :value="item.key"
                          :key="item.key">
                        </el-option>
                    </el-select>
                </el-form-item>

                <!-- <el-form-item label="" style="padding-left: 14px;">
                    <div class="block">
                        <el-date-picker v-model="filters.date" type="datetimerange" format="yyyy-MM-dd HH:mm:ss" placeholder="">
                        </el-date-picker>
                    </div>
                </el-form-item> -->
                <el-form-item>
                    <el-tooltip content="" placement="top">
                    <el-button type="primary" v-on:click="getPictureList" class="el-icon-search" ></el-button>
                    </el-tooltip>
                    <el-tooltip content="" placement="top">
                    <el-button type="primary" v-on:click="handleReset" class="el-icon-circle-cross" ></el-button>
                    </el-tooltip>
                    <el-tooltip content="" placement="top" >
                     <el-button type="primary" v-on:click="toAddPicture" class="el-icon-plus"></el-button>
                     </el-tooltip>
                </el-form-item>
            </el-form>
        </el-col>

        <!---->
        <el-col :span="24" class="toolbar">
            <font class="toolbar">:</font>
            <el-cascader
            :options="actions"
            @change="handleChange"
            placeholder="" >
            </el-cascader>
            <el-pagination
                  @current-change="handleCurrentChange"
                  :current-page="1"
                  :page-size="pageSize"
                  layout="prev, pager, next, jumper"
                  :total="total"
                   style="float:right;">
            </el-pagination>
        </el-col>

        <!---->
        <el-table :data="pictures" ref="table" highlight-current-row v-loading="listLoading"
        element-loading-text=""
        @selection-change="handleSelectionChange"
        @sort-change="handlerSortChange"
        border style="font-size:12px;" class="picture_list" >
            <el-table-column type="selection" width="50">
            </el-table-column>
            <el-table-column label="" width="113"  align="left">
              <template scope="scope">
                <a :href="scope.row.icon" target="_blank">
                    <img :src="scope.row.icon" style="height: 200px">
                </a>
              </template>
            </el-table-column>
            <el-table-column prop="title"  label=""  >
                <template scope="scope">
                    <a :href="'#/apps/edit_picture?id='+scope.row.id" target="_blank">
                       {{scope.row.title}}
                    </a>
                </template>
            </el-table-column>

            <el-table-column prop="desc"  label="" width="300">
            </el-table-column>

            <el-table-column align="center" prop="legacy_id" sortable="custom" label="ID" >
                <!-- <template scope="scope">
                    <a :href="'#/apps/edit_picture?id='+scope.row.id" target="_blank">
                       {{scope.row.legacy_id}}
                    </a>
                </template> -->
            </el-table-column>


            <el-table-column prop="isready" label="" width="30" :formatter="formatBoolean" >
            </el-table-column>

             <el-table-column prop="status" label="" width="30" >
             </el-table-column>
             <el-table-column prop="level" label="" width="50">
             </el-table-column>

            <el-table-column  label="" :formatter="formatAlbums">
            </el-table-column>

            <el-table-column prop="cp" label="CP">
            </el-table-column>

            <el-table-column prop="brand" label="" >
            </el-table-column>
            <el-table-column prop="creator_name" label="">
            </el-table-column>

            <el-table-column prop="operator_name" label="">
            </el-table-column>

            <el-table-column prop="create_time" sortable="custom" label="" width="120">
            </el-table-column>

            <el-table-column prop="update_time"  sortable="custom" label="" width="120">
            </el-table-column>

            <el-table-column prop="last_readytime"  sortable="custom" label="" width="120">
            </el-table-column>
        </el-table>

        <!---->
        <el-col :span="24" class="toolbar">
            <el-pagination
                  @current-change="handleCurrentChange"
                  :current-page="page"
                  :page-size="pageSize"
                  layout="prev, pager, next, jumper"
                  :total="total"
                   style="float:right;">
            </el-pagination>
        </el-col>

    </el-row>
</template>

<script>
    // import { downloadPic } from '../../api/api';
    import util from '../../common/js/util'
    import NProgress from 'nprogress'
    var Resource = require('../../util/Resource');

    export default {
        data() {
            return {
                filters: {
                    tag: '',
                    title: '',
                    status: '',
                    date:'',
                    id: '',
                    album_id:'',
                    creator:'',
                    isready:'',
                    cp:'',
                    brand:'',
                    sort: '-create_time',
                    category: "",
                    acc_type: "",
                    is_out: "",
                    desc:""
                },
                tags: [],
                cps: [],
                creators: [],
                brands: [],
                filter_result:{},
                pictures: [],
                total: 0,
                type: 0,
                page: 1,
                pageSize: 30,
                listLoading: true,
                sels: [],//
                actions:
                    [{
                      value: 'level',
                      label: '',
                    },{
                      value: 'tooffline',
                      label: '',
                    },{
                      value: 'toavailable',
                      label: '',
                    },{
                      value: 'toforceoffline',
                      label: '',
                    },{
                      value: 'todailyrecommend',
                      label: '',
                    },{
                      value: 'mergetopic',
                      label: '',
                    },{
                      value: 'download',
                      label: '',
                    }
                    // ,{
                    //   value: 'torecommend',
                    //   label: '',
                    // }
                    ],
            }
        },
        methods: {
            getCreators(value) {
                var _this = this;
                Resource.get({
                    resource: "album.creators",
                    data: {
                      name: value
                    },
                    success: function(data) {
                        _this.creators = data.items;
                    },
                    error: function() {
                        _this.$notify({
                            title: '',
                            message: '',
                            type: 'error'
                        });
                    }
                })
            },
            getBrands(value) {
                var _this = this;
                Resource.get({
                    resource: "brand.search_brand",
                    data: {
                      name: value
                    },
                    success: function(data) {
                        _this.brands = data.items;
                    },
                    error: function() {
                        _this.$notify({
                            title: '',
                            message: '',
                            type: 'error'
                        });
                    }
                })
            },
            getCPs(value) {
                var _this = this;
                Resource.get({
                    resource: "cp.search_cp",
                    data: {
                      name: value
                    },
                    success: function(data) {
                        _this.cps = data.items;
                    },
                    error: function() {
                        _this.$notify({
                            title: '',
                            message: '',
                            type: 'error'
                        });
                    }
                })
            },
            getTags(value) {
                var _this = this;
                Resource.get({
                    resource: "tag.search_tag",
                    data: {
                      name: value
                    },
                    success: function(data) {
                        _this.tags = data.items;
                    },
                    error: function() {
                        _this.$notify({
                            title: '',
                            message: '',
                            type: 'error'
                        });
                    }
                })
            },
            toAddPicture: function() {
                window.location.href = '/#/apps/add_picture';
            },
            handleChange: function(value) {
                if (this.sels.length < 1) {
                    this.$notify({
                        title: '',
                        message: '',
                        type: 'error'
                    });
                    return;
                }
                switch(value[0]) {
                    case "tooffline":
                        this.changeStatus("off");
                        break;
                    case "toavailable":
                        this.changeStatus("on");
                        break;
                    case "toforceoffline":
                        this.toForceOffline();
                        break;
                    case "todailyrecommend":
                        this.toDailyRecommend();
                        break;
                    case "torecommend":
                        this.toRecommend();
                        break;
                    case "mergetopic":
                        this.mergeTopic();
                        break;
                    case "download":
                        this.downloadPic();
                        break;
                    case "level":
                        this.toSetLevel();
                        break;
                }
            },
            changeStatus: function(status) {
                var _this = this;
                Resource.post({
                    resource: "album.picture_status",
                    data: {
                        ids: this.sels,
                        status: status
                    },
                    success: function(data) {
                        _this.getPictureList();
                    },
                    error: function() {
                        _this.$notify({
                            title: '',
                            message: '',
                            type: 'error'
                        });
                    }
                })
            },
            mergeTopic: function() {
                if (this.sels.length < 2) {
                    this.$notify({
                        title: '',
                        message: '',
                        type: 'error'
                    });
                    return;
                }

                var _this = this;
                Resource.put({
                    resource: "album.picture_merge",
                    data: {
                        ids: this.sels
                    },
                    success: function(data) {
                        window.location.href = '/#/apps/edit_album?id='+data.id;
                    },
                    error: function(data) {
                        var message = '';
                        if (data.data && data.data.errmsg) {
                            message = data.data.errmsg
                        }
                        NProgress.done();
                        _this.$notify({
                            title: '',
                            message: message,
                            type: 'error'
                        });
                        _this.submitVisible = false;
                    }
                });
            },
            downloadPic: function() {
                var params = {
                    ids: this.sels.join(",")
                };
                this.API_UTIL.API_PHONE.downloadPic(params);
            },
            toForceOffline: function() {
                var _this = this;
                Resource.options({
                      method: "put",
                      resource: "force_offline.force_offline",
                      data: {
                          album_ids: this.sels.join(","),
                      },
                      success: function(data) {
                          _this.$notify({
                              title: '',
                              message: '',
                              type: 'success'
                          });
                          //window.location.href = "/#/apps/edit_force_offline?id="+ data.id;
                      },
                      error: function(data) {
                          var message = '';
                          var id = 0;
                          if (data.data && data.data.errmsg) {
                              message = data.data.errmsg;
                              id = data.data.id;
                          }
                          _this.$notify({
                              title: '',
                              message: message,
                              type: 'error'
                          });
                          if (id > 0) {
                              window.location.href = "/#/apps/edit_force_offline?id="+ id;
                          }
                      }
                  });
            },
            toRecommend: function() {
                var _this = this;
                Resource.options({
                      method: "put",
                      resource: "force_recommend.force_recommend",
                      data: {
                          album_ids: this.sels.join(","),
                      },
                      success: function(data) {
                          _this.$notify({
                              title: '',
                              message: '',
                              type: 'success'
                          });
                          //window.location.href = "/#/apps/edit_force_recommend?id="+ data.id;
                      },
                      error: function(data) {
                          var message = '';
                          var id = 0;
                          if (data.data && data.data.errmsg) {
                              message = data.data.errmsg;
                              id = data.data.id;
                          }
                          _this.$notify({
                              title: '',
                              message: message,
                              type: 'error'
                          });
                          if (id > 0) {
                              window.location.href = "/#/apps/edit_force_recommend?id="+ id;
                          }
                      }
                  });
            },
            toSetLevel: function() {
                var _this = this;
                Resource.post({
                    resource: "album.picture_level",
                    data: {
                        ids: this.sels
                    },
                    success: function(data) {
                        _this.getPictureList();
                    },
                    error: function() {
                        _this.$notify({
                            title: '',
                            message: '',
                            type: 'error'
                        });
                    }
                })
            },
            toDailyRecommend: function() {
              var _this = this;
                Resource.options({
                      method: "put",
                      resource: "daily_recommend.daily_recommend",
                      data: {
                          album_ids: this.sels,
                      },
                      success: function(data) {
                          _this.$notify({
                              title: '',
                              message: '',
                              type: 'success'
                          });
                          window.location.href = "/#/apps/edit_daily_recommend?id="+ data.id;
                      },
                      error: function(data) {
                          var message = '';
                          var id = 0;
                          if (data.data && data.data.errmsg) {
                              message = data.data.errmsg;
                              id = data.data.id;
                          }
                          _this.$notify({
                              title: '',
                              message: message,
                              type: 'error'
                          });
                          if (id > 0) {
                              window.location.href = "/#/apps/edit_daily_recommend?id="+ id;
                          }
                      }
                  });
            },
            handlerSortChange: function(obj) {
                if (obj.column) {
                    if (obj.order == "descending") {
                        this.filters.sort = "-" + obj.prop;
                    } else {
                        this.filters.sort = obj.prop;
                    }
                }
                this.getPictureList();

            },
            formatBoolean: function(row, column) {
                return row.isready == true ? '' : row.isready == false ? '' : '';
            },
            formatAlbums: function(row, column) {
                return row.albums.join(",");
            },
            handleCurrentChange(val) {
                this.page = val;
                this.getPictureList();
            },
            //
            handleSelectionChange(vals) {
                this.sels = [];
                var _this = this;
                vals.forEach(function(v){
                    _this.sels.push(v.id);
                })
            },
            // 
            getSearch() {
                var _this = this;
                Resource.get({
                    resource: "album.picture_search",
                    success: function(data) {
                        _this.filter_result = data.filter_result;
                    },
                    error: function() {
                        _this.$notify({
                            title: '',
                            message: '',
                            type: 'error'
                        });
                    }
                });
            },
            getFilters() {
                if(this.filters.date){
                    var date_start = util.formatDate.format(this.filters.date[0],'y-M-d h:m:s');
                    var date_end = util.formatDate.format(this.filters.date[1],'y-M-d h:m:s');
                }else{
                    var date_start = '';
                    var date_end = '';

                }
                var page = this.page || 1;
                var title = encodeURIComponent(this.filters.title || '');
                var date_start = date_start || '';
                var date_end = date_end || '';
                var status = this.filters.status;
                var id = this.filters.id || '';
                var album_id = this.filters.album_id || '';
                var isready = this.filters.isready;
                var cp_id = this.filters.cp || '';
                var brand_id = this.filters.brand || '';
                var tag = this.filters.tag || '';
                var creator = this.filters.creator || '';
                var category = this.filters.category || '';
                var acc_type = this.filters.acc_type || '';
                var is_out = this.filters.is_out || '';
                var sort = this.filters.sort || '-create_time';
                var desc = this.filters.desc || '';
                return {
                    page: page,
                    title: title,
                    date_start: date_start,
                    date_end: date_end,
                    status: status,
                    id: id,
                    album_id: album_id,
                    isready: isready,
                    cp_id: cp_id,
                    brand_id: brand_id,
                    type: this.type,
                    tag: tag,
                    creator: creator,
                    category: category,
                    acc_type: acc_type,
                    is_out: is_out,
                    sort: sort,
                    desc:desc
                }
            },
            handleReset() {
                this.filters={
                    tag: '',
                    creator: '',
                    title: '',
                    status: '',
                    date:'',
                    id: '',
                    album_id:'',
                    isready:'',
                    cp:'',
                    brand:'',
                    sort: '-create_time',
                    acc_type: '',
                    is_out: '',
                    desc: ''
                },
                this.getPictureList();
            },
            //
            getPictureList() {
                this.pictures = [];
                this.listLoading = true;
                let para = this.getFilters();
                var _this = this;
                NProgress.start();
                Resource.get({
                    resource: "album.picture_list",
                    data: para,
                    success: function(data) {
                        _this.total = data.total_count;
                        _this.pictures = data.items;
                        _this.listLoading = false;
                        NProgress.done();
                    },
                    error: function() {
                        _this.listLoading = false;
                        _this.$notify({
                            title: '',
                            message: '',
                            type: 'error'
                        });
                    }
                })
            },
            removeUser: function(para){
                Resource.delete({
                    resource: "album.picture",
                    data: para,
                    success: function(data) {
                        return true;
                    },
                    error: function() {
                        return false;
                    }
                })
            },
        },
        mounted() {
            this.getSearch();
            this.getPictureList();

        }
    }

</script>

<style scoped>
.el-table .cell, .el-table th>div {
    height: 201px!important;
}
</style>



