<template>
    <section style="left: 185px;">
        <!---->
        <el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
            <el-form :inline="true" :model="filters">
                <el-form-item label="" >
                    <el-input v-model="filters.title" style="width: 200px;" ></el-input>
                </el-form-item>
                 <el-form-item label="" >
                    <el-input v-model="filters.desc" style="width: 180px;"></el-input>
                </el-form-item>
                <el-form-item label="ID" style="padding-left: 11px;">
                    <el-input v-model="filters.album_id" ></el-input>
                </el-form-item>

                <!-- <el-form-item label="" style="padding-left: 28px;">
                    <el-select v-model="filters.isready" placeholder="" style="width: 120px;">
                        <el-option
                          v-for="item in filter_result.isready"
                          :label="item.value"
                          :value="item.key"
                          :key="item.key">
                        </el-option>
                    </el-select>
                </el-form-item> -->
                <el-form-item label="" >
                    <el-select v-model="filters.status" placeholder="" style="width: 180px;">
                        <el-option
                          v-for="item in filter_result.status"
                          :label="item.value"
                          :value="item.key"
                          :key="item.key">
                        </el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="" style="padding-left: 10px;">
                    <el-select v-model="filters.is_out" placeholder="" style="width: 180px;">
                        <el-option
                          v-for="item in filter_result.is_out"
                          :label="item.value"
                          :value="item.key"
                          :key="item.key">
                        </el-option>
                    </el-select>
                </el-form-item>
                <br>
                <el-form-item label="" prop="creator" >
                     <el-select
                       v-model="filters.creator"
                       filterable
                       remote
                       placeholder=""
                       :remote-method="getCreators" style="padding-left: 13px;">
                        <el-option
                            v-for="item in creators"
                            :key="item.id"
                            :label="item.name"
                            :value="item.id">
                        </el-option>
                     </el-select>
                </el-form-item>
                <el-form-item label="CP" prop="cp" >
                     <el-select
                       v-model="filters.cp"
                       filterable
                       remote
                       placeholder="CP"
                       :remote-method="getCPs"
                       style="width:182px;">
                        <el-option
                            v-for="item in cps"
                            :key="item.id"
                            :label="item.name"
                            :value="item.id">
                        </el-option>
                     </el-select>
                </el-form-item>
                <el-form-item label="" prop="brand">
                   <el-select
                       v-model="filters.brand"
                       filterable
                       remote
                       placeholder=""
                       :remote-method="getBrands"
                       style="width: 180px;">
                       <el-option
                         v-for="item in brands"
                         :key="item.id"
                         :label="item.name"
                         :value="item.id">
                       </el-option>
                     </el-select>
                 </el-form-item>
                <!-- <el-form-item label="">
                    <div class="block">
                        <el-date-picker v-model="filters.date" type="datetimerange" format="yyyy-MM-dd HH:mm:ss" placeholder="" style="width: 322px;">
                        </el-date-picker>
                    </div>
                </el-form-item> -->
                <el-form-item>
                    <el-tooltip content="" placement="top">
                    <el-button type="primary" v-on:click="getAlbumList" class="el-icon-search"></el-button>
                    </el-tooltip>
                    <el-tooltip content="" placement="top">
                    <el-button type="primary" v-on:click="handleReset" class="el-icon-circle-cross" ></el-button>
                    </el-tooltip>
                    <el-tooltip content="" placement="top" >
                     <el-button type="primary" v-on:click="toAddAlbum" class="el-icon-plus"></el-button>
                     </el-tooltip>
                </el-form-item>
            </el-form>
        </el-col>

        <!---->
        <el-col :span="24" class="toolbar">
            <font class="toolbar">:</font>
            <el-cascader
            :options="actions"
            @change="handleChange"
            placeholder="">
            </el-cascader>
            <el-pagination
                  @current-change="handleCurrentChange"
                  :current-page="1"
                  :page-size="pageSize"
                  layout="prev, pager, next, jumper"
                  :total="total"
                   style="float:right;">
            </el-pagination>
        </el-col>

        <!---->
        <el-table :data="albums" highlight-current-row element-loading-text=""
        @selection-change="handleSelectionChange"
        @sort-change="handlerSortChange" v-loading="listLoading"  border style="font-size:12px; ">
            <el-table-column type="selection" width="50">
            </el-table-column>
            <el-table-column label="" width="113" align="left">
              <template scope="scope">
                <a :href="scope.row.icon" target="_blank">
                    <img :src="scope.row.icon" style="height: 200px">
                </a>
              </template>
            </el-table-column>
            <el-table-column prop="title"  label=""  >
                <template scope="scope">
                    <a :href="'#/apps/edit_album?id='+scope.row.id" target="_blank">
                       {{scope.row.title}}
                    </a>
                </template>
            </el-table-column>

            <el-table-column prop="desc"  label="" width="300">
            </el-table-column>

            <el-table-column prop="legacy_id" label="ID" sortable="custom">
                <template scope="scope">
                    <a :href="'#/apps/edit_album?id='+scope.row.id" target="_blank">
                       {{scope.row.legacy_id}}
                    </a>
                </template>
            </el-table-column>


            <el-table-column prop="isready" label="" width="30" :formatter="formatBoolean" ></el-table-column>

             <el-table-column prop="status" label="" width="30" ></el-table-column>

             <el-table-column prop="level" label="" width="50" ></el-table-column>


            <el-table-column prop="cp" label="CP"> </el-table-column>

            <el-table-column prop="brand" label="" ></el-table-column>

            <el-table-column prop="creator_name" label=""> </el-table-column>

            <el-table-column prop="operator_name" label="">
            </el-table-column>

            <el-table-column prop="create_time" label="" width="120" sortable
            ></el-table-column>

            <el-table-column prop="update_time" label="" width="120" sortable
            ></el-table-column>

            <el-table-column prop="last_readytime" label="" width="120" sortable
            ></el-table-column>
        </el-table>

        <!---->
        <el-col :span="24" class="toolbar">
            <el-pagination
                  @current-change="handleCurrentChange"
                  :current-page="1"
                  :page-size="pageSize"
                  layout="prev, pager, next, jumper"
                  :total="total"
                   style="float:right;">
            </el-pagination>
        </el-col>
    </section>
</template>

<script>
    import util from '../../common/js/util'
    import NProgress from 'nprogress'
    var Resource = require('../../util/Resource');

    export default {
        data() {
            return {
                filters: {
                    title: '',
                    status: '',
                    date:'',
                    album_id:'',
                    isready:'',
                    cp:'',
                    brand:'',
                    sort: '-create_time',
                    creator:'',
                    is_out: '',
                    desc:''
                },
                cps: [],
                creators: [],
                brands: [],
                filter_result:{},
                albums: [],
                total: 0,
                type: 1,
                page: 1,
                pageSize: 30,
                listLoading: true,
                sels: [],//
                actions:
                    [{
                      value: 'tooffline',
                      label: '',
                    },{
                      value: 'toavailable',
                      label: '',
                    },{
                      value: 'toforceoffline',
                      label: '',
                    },{
                      value: 'todailyrecommend',
                      label: '',
                    },{
                      value: 'level',
                      label: '',
                    },
                    // {
                    //   value: 'torecommend',
                    //   label: '',
                    // }
                    ],
            }
        },
        methods: {
            getCreators(value) {
                var _this = this;
                Resource.get({
                    resource: "album.creators",
                    data: {
                      name: value
                    },
                    success: function(data) {
                        _this.creators = data.items;
                    },
                    error: function() {
                        _this.$notify({
                            title: '',
                            message: '',
                            type: 'error'
                        });
                    }
                })
            },
            getBrands(value) {
                var _this = this;
                Resource.get({
                    resource: "brand.search_brand",
                    data: {
                      name: value
                    },
                    success: function(data) {
                        _this.brands = data.items;
                    },
                    error: function() {
                        _this.$notify({
                            title: '',
                            message: '',
                            type: 'error'
                        });
                    }
                })
            },
            getCPs(value) {
                var _this = this;
                Resource.get({
                    resource: "cp.search_cp",
                    data: {
                      name: value
                    },
                    success: function(data) {
                        _this.cps = data.items;
                    },
                    error: function() {
                        _this.$notify({
                            title: '',
                            message: '',
                            type: 'error'
                        });
                    }
                })
            },
            toAddAlbum: function() {
                window.location.href = '/#/apps/add_album';
            },
            handleChange: function(value) {
                if (this.sels.length < 1) {
                    this.$notify({
                        title: '',
                        message: '',
                        type: 'error'
                    });
                    return;
                }
                switch(value[0]) {
                    case "tooffline":
                        this.changeStatus("off");
                        break;
                    case "toavailable":
                        this.changeStatus("on");
                        break;
                    case "toforceoffline":
                        this.toForceOffline();
                        break;
                    case "todailyrecommend":
                        this.toDailyRecommend();
                        break;
                    case "torecommend":
                        this.toRecommend();
                        break;
                    case "level":
                        this.toSetLevel();
                        break;
                }
            },
            changeStatus: function(status) {
                var _this = this;
                Resource.post({
                    resource: "album.status",
                    data: {
                        ids: this.sels,
                        status: status
                    },
                    success: function(data) {
                        _this.getAlbumList();
                    },
                    error: function() {
                        _this.$notify({
                            title: '',
                            message: '',
                            type: 'error'
                        });
                    }
                })
            },
            toForceOffline: function() {
                var _this = this;
                Resource.options({
                      method: "put",
                      resource: "force_offline.force_offline",
                      data: {
                          album_ids: this.sels.join(","),
                      },
                      success: function(data) {
                          _this.$notify({
                              title: '',
                              message: '',
                              type: 'success'
                          });
                          window.location.href = "/#/apps/edit_force_offline?id="+ data.id;
                      },
                      error: function(data) {
                          var message = '';
                          var id = 0;
                          if (data.data && data.data.errmsg) {
                              message = data.data.errmsg;
                              id = data.data.id;
                          }
                          _this.$notify({
                              title: '',
                              message: message,
                              type: 'error'
                          });
                          if (id > 0) {
                              window.location.href = "/#/apps/edit_force_offline?id="+ id;
                          }
                      }
                  });
            },
            toDailyRecommend: function() {
              var _this = this;
                Resource.options({
                      method: "put",
                      resource: "daily_recommend.daily_recommend",
                      data: {
                          album_ids: this.sels,
                      },
                      success: function(data) {
                          _this.$notify({
                              title: '',
                              message: '',
                              type: 'success'
                          });
                      },
                      error: function(data) {
                          var message = '';
                          var id = 0;
                          if (data.data && data.data.errmsg) {
                              message = data.data.errmsg;
                              id = data.data.id;
                          }
                          _this.$notify({
                              title: '',
                              message: message,
                              type: 'error'
                          });
                          if (id > 0) {
                              window.location.href = "/#/apps/edit_daily_recommend?id="+ id;
                          }
                      }
                  });
            },
            toRecommend: function() {
                var _this = this;
                Resource.options({
                      method: "put",
                      resource: "force_recommend.force_recommend",
                      data: {
                          album_ids: this.sels.join(","),
                      },
                      success: function(data) {
                          _this.$notify({
                              title: '',
                              message: '',
                              type: 'success'
                          });
                          //window.location.href = "/#/apps/edit_force_recommend?id="+ data.id;
                      },
                      error: function(data) {
                          var message = '';
                          var id = 0;
                          if (data.data && data.data.errmsg) {
                              message = data.data.errmsg;
                              id = data.data.id;
                          }
                          _this.$notify({
                              title: '',
                              message: message,
                              type: 'error'
                          });
                          if (id > 0) {
                              window.location.href = "/#/apps/edit_force_recommend?id="+ id;
                          }
                      }
                  });
            },
            toSetLevel: function() {
                var _this = this;
                Resource.post({
                    resource: "album.level",
                    data: {
                        ids: this.sels
                    },
                    success: function(data) {
                        _this.getAlbumList();
                    },
                    error: function() {
                        _this.$notify({
                            title: '',
                            message: '',
                            type: 'error'
                        });
                    }
                })
            },
            handlerSortChange: function(obj) {
                if (obj.column) {
                    if (obj.order == "descending") {
                        this.filters.sort = "-" + obj.prop;
                    } else {
                        this.filters.sort = obj.prop;
                    }
                }
                this.getAlbumList();
            },
            formatBoolean: function(row, column) {
                return row.isready == true ? '' : row.isready == false ? '' : '';
            },
            handleCurrentChange(val) {
                this.page = val;
                this.getAlbumList();
            },
            handleSelectionChange(vals) {
                this.sels = [];
                var _this = this;
                vals.forEach(function(v){
                    _this.sels.push(v.id);
                })
            },
            handleReset() {
                this.filters={
                    title: '',
                    status: '',
                    date:'',
                    album_id:'',
                    isready:'',
                    cp:'',
                    brand:'',
                    sort: '-create_time',
                    creator: '',
                    desc:''
                },
                this.getAlbumList();
            },
            // 
            getSearch() {
                var _this = this;
                Resource.get({
                    resource: "album.picture_search",
                    success: function(data) {
                        _this.filter_result = data.filter_result;
                    },
                    error: function() {
                        _this.$notify({
                            title: '',
                            message: '',
                            type: 'error'
                        });
                    }
                });
            },
            getFilters() {
                if(this.filters.date){
                    var date_start = util.formatDate.format(this.filters.date[0],'y-M-d h:m:s');
                    var date_end = util.formatDate.format(this.filters.date[1],'y-M-d h:m:s');
                }else{
                    var date_start = '';
                    var date_end = '';

                }
                var page = this.page || 1;
                var title = encodeURIComponent(this.filters.title || '');
                var date_start = date_start || '';
                var date_end = date_end || '';
                var status = this.filters.status;
                var album_id = this.filters.album_id || '';
                var isready = this.filters.isready;
                var cp_id = this.filters.cp || '';
                var brand_id = this.filters.brand || '';
                var creator = this.filters.creator || '';
                var is_out = this.filters.is_out || '';
                var sort = this.filters.sort || '-create_time';
                var desc = this.filters.desc || '';
                return {
                    page: page,
                    title: title,
                    date_start: date_start,
                    date_end: date_end,
                    status: status,
                    album_id: album_id,
                    isready: isready,
                    cp_id: cp_id,
                    brand_id: brand_id,
                    type: this.type,
                    creator: creator,
                    is_out: is_out,
                    sort: sort,
                    desc:desc
                }
            },
            //
            getAlbumList() {
                this.listLoading = true;
                let para = this.getFilters();
                NProgress.start();
                var _this = this;
                Resource.get({
                    resource: "album.picture_list",
                    data: para,
                    success: function(data) {
                        _this.total = data.total_count;
                        _this.albums = data.items;
                        _this.listLoading = false;
                        NProgress.done();
                    },
                    error: function() {
                        _this.listLoading = false;
                        NProgress.done();
                        _this.$notify({
                            title: '',
                            message: '',
                            type: 'error'
                        });
                    }
                })
            }
        },
        mounted() {
            this.getSearch();
            this.getAlbumList();
        }
    }

</script>

<style lang="scss" scoped>
.el-table td>div {
    height:201px!important;
}
</style>



